import {ButtonCounter} from "../buttons/ButtonCounter";
import React from "react";
import {FormControl, InputLabel, MenuItem, Select} from "@mui/material";
import {Button} from "../buttons/Button";
import {useDispatch} from "react-redux";
import {
    handlerAddChild,
    handlerAddGuest,
    handlerDelChild,
    handlerDelGuest,
    handlerEditOldChild
} from "../../store/HotelItem";
import {RED} from "../../theme/colors";

export const GuestHotelNumber = ({style, guest, child, handler, checkOld, styles, styles_content}) => {
    const dispatch = useDispatch()
    const old = [
        {id: 0, name: " ",}, {id: 1, name: "1 ",}, {id: 2, name: "2 ",},
        {id: 3, name: "3 ",}, {id: 4, name: "4 ",}, {id: 5, name: "5 ",},
        {id: 6, name: "6 ",}, {id: 7, name: "7 ",}, {id: 8, name: "8 ",},
        {id: 9, name: "9 ",}, {id: 10, name: "10 ",}, {id: 11, name: "11 ",},
        {id: 12, name: "12 ",}, {id: 13, name: "13 ",}, {id: 14, name: "14 ",},
        {id: 15, name: "15 ",}, {id: 16, name: "16 ",}, {id: 17, name: "17 ",},
    ]

    const handlerChooseOld = (e, id) => {

        dispatch(handlerEditOldChild({idChild: id, old: e.target.value}))
    }

    return (
        <div
            className={style}

        >
            <div
                className="modal__content__guest"
                style={styles}
            >
                <div className="modal__body">
                    <div
                        className="column__fs"
                        style={{
                            marginBottom: "10px",
                            marginLeft: "5px",
                            marginRight: "10px"
                        }}
                    >

                        <div>
                            <div className="column__fs">
                                <div
                                    className="row__sb__c"
                                    style={{
                                        marginTop: "10px",
                                        marginBottom: "5px",
                                        borderBottom: "1px solid var(--grey-white)",
                                        paddingBottom: "10px"
                                    }}
                                >
                                    <div className="column__fs">
                                        <span className="text__content__black__14"></span>
                                        <span className="text__content__grey__12"> 18 </span>
                                    </div>
                                    <ButtonCounter
                                        count={guest}
                                        style={"row__sb__c counterGuestBtn"}
                                        handleAdd={() => dispatch((handlerAddGuest(1)))}
                                        handleDel={() => dispatch((handlerDelGuest(1)))}
                                    />
                                </div>
                                <div
                                    className="row__sb__c"
                                    style={{
                                        marginTop: "5px",
                                        marginBottom: "10px",
                                        borderBottom: "1px solid var(--grey-white)",
                                        paddingBottom: "10px"
                                    }}
                                >
                                    <div className="column__fs">
                                        <span className="text__content__black__14"></span>
                                        <span className="text__content__grey__12"> 17 </span>
                                    </div>
                                    <ButtonCounter
                                        handleAdd={() => dispatch((handlerAddChild(child.length + 1)))}
                                        handleDel={() => dispatch((handlerDelChild(child.length - 1)))}
                                        count={child.length}
                                        style={"row__sb__c counterGuestBtn"}
                                    />
                                </div>
                                {child.length === 0 ?
                                    ""
                                    :
                                    child.map(item => (
                                        <div
                                            className="row__sb__c"
                                            key={item.id}
                                            style={{
                                                marginTop: "5px",
                                                marginBottom: "10px",
                                                borderBottom: "1px solid var(--grey-white)",
                                                paddingBottom: "10px"
                                            }}
                                        >

                                            <FormControl sx={{m: 1, width: "100%"}} size="small">
                                                <InputLabel id="demo-select-small-label"></InputLabel>
                                                <Select
                                                    labelId="demo-select-small-label"
                                                    id="demo-select-small"
                                                    value={item.old}
                                                    label=""
                                                    onChange={(e) => handlerChooseOld(e, item.id)}
                                                >
                                                    <MenuItem  value="">
                                                        { checkOld ? <b style={{color: RED}}> </b> : <b></b>}
                                                    </MenuItem>
                                                    {old.map(old => (
                                                        <MenuItem
                                                            value={old.id}
                                                            key={old.id}
                                                        > {old.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                        </div>
                                    ))
                                }
                            </div>

                        </div>
                        <div
                            className="row__c__c"
                        >
                            <Button
                                style={"doneBtn"}
                                name={""}
                                handler={handler}
                                styleText={"text__content__white__16"}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}