export function getAccommodationCity(city) {
    const lastChar = city[city.length - 1];
    const lastTwoChars = city.slice(-2);

    if (lastTwoChars === "" || lastTwoChars === "" || lastTwoChars === "" || lastTwoChars === ""|| lastTwoChars === "") {
        return city + "";
    } else if (lastChar === "") {
        return city.slice(0, -1) + "";
    } else {
        return city;
    }
}