export function parseJSONProperties(obj) {
    //   ,     
    const newObj = { ...obj };

    //    
    for (let key in newObj) {
        // ,     
        if (typeof newObj[key] === 'string') {
            try {
                //      JSON
                newObj[key] = JSON.parse(newObj[key]);
            } catch (error) {
                //    ,    
            }
        }
    }

    return newObj;
}

export const parseJSONData = (jsonData) => {
    try {
        const parsedData = JSON.parse(jsonData);
        return parsedData;
    } catch (error) {
        console.error("Error parsing JSON data:", error);
        return null;
    }
};

export function parseJSONData2(jsonData) {
    try {
        const parsedData = JSON.parse(jsonData);
        return parsedData;
    } catch (error) {
        console.error("   JSON :", error);
        return null;
    }
}

export function parseJSONPropertiesInArray(arr) {
    //       
    const newArr = [];

    //     
    for (let i = 0; i < arr.length; i++) {
        const obj = arr[i];

        //       
        const newObj = {};

        //     
        for (let key in obj) {
            // ,     
            if (typeof obj[key] === 'string') {
                try {
                    //      JSON
                    newObj[key] = JSON.parse(obj[key]);
                } catch (error) {
                    //    ,    
                    newObj[key] = obj[key];
                }
            } else {
                //     ,    
                newObj[key] = obj[key];
            }
        }

        //      
        newArr.push(newObj);
    }

    return newArr;
}